/* source-serif-4-300 - latin */
@font-face {
  font-display: fallback;
  font-family: 'Source Serif 4';
  font-style: normal;
  font-weight: 300;
  src: url('../fonts/source-serif-4-v7-latin-300.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}

/* source-serif-4-300italic - latin */
@font-face {
  font-display: fallback;
  font-family: 'Source Serif 4';
  font-style: italic;
  font-weight: 300;
  src: url('../fonts/source-serif-4-v7-latin-300italic.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}

